<template>
    <div class="home">
        <div class="bannerBox">
           <div class="swiper-container bannerHeight">
                <div class="swiper-wrapper">
                    <div 
                    class="swiper-slide " 
                    v-for="(BG,index) in Banners"
                    v-bind:style="{'background-color':BG.Img}" >
                        <div>
                            {{BG.title}}
                        </div>
                    </div>
                </div>
                <div class="swiper-pagination "></div>
                <div class="swiper-button-box">
                    <div class="swiper-button-prev"><</div>
                    <div class="swiper-button-next">></div>
                </div>
            </div>
        </div>
        <div class="main">
            <div class="main-modular">
                <div class="main-modular-title">
                    <span></span>
                    <div></div>
                </div>
                <div class="main-modular-content">
                    <div class="content-left">

                    </div>
                    <div class="content"></div>
                    <div class="content-left"></div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>

    export default{
        name: 'home',
        data(){
            return {
                Banners:[
                    {
                        Img:'gray',
                        Url:'',
                        title:'1'
                    },
                    {
                        Img:'black',
                        Url:'',
                        title:'2'
                    },
                    {
                        Img:'red',
                        Url:'',
                        title:'3'
                    },
                    {
                        Img:'gray',
                        Url:'',
                        title:'1'
                    },
                    {
                        Img:'black',
                        Url:'',
                        title:'2'
                    },
                    {
                        Img:'red',
                        Url:'',
                        title:'3'
                    }
                ]
            }
        },
        mounted(){
            var mySwiper = new Swiper('.swiper-container', {
                direction : 'horizontal',//'vertical' horizontal,
                speed:400, //
                autoplay: 2000, //
                effect : 'fade',//
                loop : true,
                fade: {
                   crossFade: false,//
                },
                prevButton:'.swiper-button-prev',
                nextButton:'.swiper-button-next',
                pagination : '.swiper-pagination',
                paginationClickable :true,
               
            });
            var spanList = document.getElementsByClassName('swiper-pagination-bullet')
            for(var i=0;i<spanList.length;i++){
                spanList[i].innerHTML=this.Banners[i].title
            }
        }
    }
</script>
<style scoped>
.bannerHeight{
    height: 430px;
    background-color: rgba(0,0,0,.1)
}
.swiper-button-box{
    position: absolute;
    width: 1200px;
    height: 0px;
    top: 50%;
    left: 50%;
    margin-left: -600px;
    font-size: 60px;
    font-weight: bold;
    color: #a3a6a9;
    line-height: 44px;
}
.swiper-button-box div{
    background: none;
}
.swiper-container .swiper-pagination-bullets{
    width: 1100px;
    left: 50%;
    margin-left: -550px;
    bottom: 0px;
    height: 40px;
    line-height: 40px;
    font-size: 14px;
    color: #000;
    text-align: center;
    display: flex; 
}
.swiper-container .swiper-pagination-bullet-active{
    float: left;
    flex: 1;
    background-color: #fff;
    border: 1px solid #e5e7e7
}
.main-modular-title{
    font-size: 24px;
    text-align: center;
    padding: 20px 0;
}
.main-modular-title span{
    display: inline-block;
    padding: 3px 10px;
    background-color: #fff;
    color: #999999;

}
.main-modular-title div{
    height: 1px;
    width: 100%;
    background-color: #e1e1e1;
    margin-top: -18px;
}

.main-modular-content{
    margin-top: 20px;
    width: 100%;
    height: 530px;
}
.content-left{
    float: left;
    width: 224px;
    height: 100%;
    background-color: #f4f7f7;
}

.content{
    float: left;
    width: 652px;
    height: 100%;
    background-color: #fff;
}


@media only screen and (min-width:320px) and (max-width: 1100px) {

	 .bannerHeight{
        height: 30vw;
        background-color: rgba(0,0,0,.1)
    }

}
    

/*.bannerBox{
    width: 100%;
    height: 200px;
    background-color: rgba(0,0,0,.1)
}*/
</style>


